/* SalePage.css */

body, html {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background: #fff;
  color: #111;
}

.sale-container {
  display: flex;
  flex-direction: column;
}

.top-banner {
  display: flex;
  justify-content: space-between;
  padding: 8px 16px;
  font-size: 16px;
  background-color: #f5f5f5;
  border-bottom: 1px solid #ddd;
}
.time{
  margin-right: 12px;
}

.time-label {
  font-size: 12px;
  font-weight: normal;
}

.sec-label {
  font-size: 10px;
  font-weight: normal;
  position: relative;
  top: 2px;
}


.promo-text {
  text-align: center;
  flex-grow: 1;
   text-transform: uppercase;
}


.shop-now{
   text-transform: uppercase;
}

.main-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px;
  border-bottom: 1px solid #eee;
  font-weight: bold;
}

.nav-left a,
.nav-right a {
  margin: 0 10px;
  text-decoration: none;
  color: #111;
  font-size: 14px;
}

.logo {
  font-weight: 1000;
  font-size: 20px;
}

.product-page {
  display: flex;
  align-items: flex-start;
  padding: 20px;
  margin-left: 20px;
}

.thumbnails {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-right: 450px;
  margin-top: 100px;
}

.thumb-img {
  width: 50px;
  height: 60px;
  cursor: pointer;
  filter: blur(1px);
}

.thumb-img:hover{
  transform: scale(1.1);
  border-color: #000;
}

.main-image img {
  width: 500px;
  height: 550px;
  margin-top: 20px;
}

.main-image:hover{
  transform: scale(1.1);
  border-color: #000;
}

.product-details {
  max-width: 300px;
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-left: 150px;
}

.product-details h2 {
  font-size: 18px;
  margin: 0;
}

.price {
  font-weight: bold;
}

.stock {
  font-size: 12px;
  color: rgb(108, 105, 105);
}

.size-image-row {
  display: flex;
  gap: 12px;
  margin-top: 12px;
  margin-bottom: 8px;
}

.size-img {
  width: 60px;
  height: 80px;
  object-fit: cover;
  border: 1px solid black;
  border-radius: 4px; 
}
.size-img:hover{
  transform: scale(1.1);
  border-color: #000;
}

.size-labels-row {
  display: flex;
  gap: 0;
  margin-bottom: 12px;
}

.size-label {
  padding: 6px 14px;
  font-weight: bold;
  color: rgb(108, 105, 105);
}

.size-guide{
  text-decoration: underline;
  font-weight: bold;
  font-size: 0.9rem; 
  cursor: pointer;   
  margin-bottom: 12px; 
}
.add-to-cart {
  padding: 10px;
  border: 2px solid #000;
  background: #fff;
  cursor: pointer;
  text-transform: uppercase;
  font-weight: bold;
}

.add-to-cart:hover{
  background: #000;
  color: #fff;
}

.description {
  font-size: 12px;
  line-height: 1.6;
}

.info-links p {
  font-size: 12px;
  text-decoration: underline;
  cursor: pointer;
  margin: 4px 0;
  line-height: 2;
  font-weight: bold;
}

.you-may-also-like {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 12px;
  margin-top: 20px;
  font-weight: bold;
  margin-bottom: 10px;
}

.arrows span {
  margin-left: 8px;
  cursor: pointer;
  font-weight: bold;
}


.you-may-images {
  display: flex;
  gap: 12px;
  justify-content: flex-start;
}

.suggested-img {
  width: 220px;       /* small width */
  height: 140px;      /* proportional height */
  object-fit: cover;
  border-radius: 4px;
  cursor: pointer;
  border: 1px solid #ddd;
  transition: transform 0.2s ease;
}

.suggested-img:hover {
  transform: scale(1.1);
  border-color: #000;
}


@media (max-width: 768px) {
  .product-page {
    flex-direction: column;
    align-items: center;
    padding: 16px;
    margin-left: 0;
  }

  .thumbnails {
    flex-direction: row;
    margin: 16px 0;
    justify-content: center;
    gap: 8px;
    margin-right: 0;
    margin-top: 0;
  }

  .thumb-img {
    width: 40px;
    height: 50px;
  }

  .main-image img {
    width: 90vw;
    height: auto;
    margin-top: 10px;
  }

  .product-details {
    margin-left: 0;
    max-width: 90%;
    align-items: center;
    text-align: center;
  }

  .size-image-row,
  .size-labels-row {
    justify-content: center;
    flex-wrap: wrap;
  }

  .you-may-also-like {
    flex-direction: row;
    align-items: flex-start;
    gap: 8px;
  }
  .arrows{
    margin-top: 10px;
  }

.you-may-images {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    gap: 12px;
    padding: 0 16px;
  }

  .suggested-img {
    width: calc(50% - 12px);
    height: auto;
    max-width: 100%;
  }

  .main-header {
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }

  .nav-left,
  .nav-right {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 10px;
  }

  .top-banner {
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 4px;
  }

  .promo-text {
    font-size: 14px;
  }
}
